:root{
  --primary: #fafaf0;
  --secondary: #a01818;
}


.App {
  text-align: center;
  font-family: 'Gloria Hallelujah', cursive;
  
}


@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background: url(../public/img/background.jpg);
}
.meme {
  background: none;
}

h1 {
  color: var(--primary);
  font-size: 2.5rem;
}

.meme {
  height: 80%;
  width: 80%;
  margin: auto;
  margin-bottom: 200px;
  position: relative;
}
.meme span {
  position: absolute;
  width: 100%;
  left: 0;
  margin-top: 5%;
  color: var(--primary);
  font-size: 2rem;
  text-shadow: 2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
}

#spanTwo {
  margin-top: 40%;
}


.meme img {
  width: 50%;

}

.footer {
  width: 100%;
  height: 80px;
  color: var(--primary);
  
}


input[type=text] {
  width: 300px;
  text-align: center;
  background: transparent;
  color: var(--primary);
  border: none;
  border-bottom: 2px solid var(--secondary);
}

input[type=text]:focus {
  background-color: rgba(179, 184, 185, 0.856);
  border: none;
  outline: none;
}

select, button{
  width: 200px;
  height: 30px;
  padding: 5px;
  color: var(--primary);
  background-color: rgba(179, 184, 185, 0.856);
  border: none;
  border-radius: 10px;
}

button {
  width: 150px;
  transition: all .2s ease-in;
}

button:hover, a:hover{
  background: var(--secondary);
  transition: all .2s ease-in;
}

select{
  color: var(--secondary);
}
a {
  color: inherit;
  text-decoration: none;
}

@media screen and (max-width: 800px){

    .meme img {
      width: 100%;
    }

    .meme span {
      margin-top: 10%;
    
    }

    #spanTwo {
      margin-top: 80%;
    }
  

}